<template>
  <div class="keyboard">
    <home-button />

    <!-- 音乐播放按钮 -->
    <div class="play" id="swq" :class="playMusic ? 'turn' : ''" @click="over">
      <svg-icon icon-class="Music" style="width: 4em; height: 4em" />
    </div>

    <div class="keyboard-con">
      <!-- 1.顶部文字显示 -->
      <div class="show-box">
        <span>{{ nowText }}</span> <span class="jump"></span>
      </div>

      <!-- 2.底部主体键盘 -->
      <div class="main-box">
        <div class="ul" v-for="(item, index) in keyBoardList" :key="index">
          <div
            v-for="(second, i) in item.details"
            :key="i"
            class="li"
            :style="getStyle(second)"
          >
            {{ second.value }}
          </div>
        </div>
      </div>
      <!-- 3.音乐 -->
      <audio
        ref="music"
        id="music"
        :src="musicUrl"
        controls="controls"
        loop="true"
        hidden="true"
      ></audio>
    </div>
  </div>
</template>

<script>
import HomeButton from "@/components/HomeButton/index";

import { keyBoardList } from "./config";
export default {
  data() {
    return {
      // 键盘配置文件
      keyBoardList,
      // 会被打印文字
      // text: "I want to see you.  oh my Baby.",
      text: "Happy Birthday.",
      // 展示的文字
      nowText: "",
      //  定时器
      times: null,

      // 音乐地址
      musicUrl: require("../../assets/video/wobushiliudehua.mp3"),
      // 音乐转动变量
      playMusic: false,
    };
  },
  components: {
    HomeButton,
  },
  created() {
    // 初始化一下数据列表
    this.keyBoardList = keyBoardList;
  },
  methods: {
    // 获取元素样式
    getStyle(second) {
      return {
        width: second.width + "px",
        height: second.height + "px",
        "line-height": second.height + "px",
        background: second.background,
        color: second.color,
        border: `2px solid ${second.borderColor}`,
      };
    },

    // 开始执行打印机动画
    showText() {
      if (this.text && this.text.length > 0) {
        // 1.截取字符串为数组
        this.text = this.text.split("");

        // 遍历数组开始动画
        let num = 0;
        this.times = setInterval(() => {
          if (num >= this.text.length) {
            clearInterval(this.times);
            return;
          }

          if (this.text[num].match(/^[ ]*$/)) {
            // 空格暂停两秒
            this.nowText += " ";
          } else {
            this.keyBoardList.forEach((item) => {
              item.details.forEach((ele) => {
                if (ele.value.toUpperCase() === this.text[num].toUpperCase()) {
                  // 如果前后都是相同的字母
                  if (ele.background === "#0C0C0E") {
                  }
                  ele.background = "#0C0C0E";
                  ele.color = ele.backgroundCopy;
                  this.nowText += this.text[num];
                } else {
                  ele.background = ele.backgroundCopy;
                  ele.color = "#0C0C0E";
                }
              });
            });
          }

          num++;
        }, 500);
      }
    },

    // 切换音乐播放效果
    over() {
      let music = document.getElementById("music");
      if (music.paused) {
        music.play();
        this.playMusic = true;
      } else {
        music.pause();
        this.playMusic = false;
      }
    },
  },
  mounted() {
    // let music = document.getElementById("music");
    // music.play();
    // this.playMusic = true;
    this.showText();
  },

  // 销毁钩子
  beforeDestroy() {
    // 页面销毁，定时器不会清除，需要手动，在销毁钩子中清除
    clearInterval(this.times);

    // 关闭音乐
    let music = this.$refs.music;
    if (!music.paused) {
      music.pause();
    }
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.keyboard {
  position: relative;
  width: 100%;
  height: 100%;
  background: $home-bc;

  .play {
    position: absolute;
    right: 5%;
    top: 5%;
    width: 4em;
    height: 4em;
    z-index: 20;
    cursor: pointer;
  }

  .turn {
    animation: turn 4s linear infinite;
  }

  @keyframes turn {
    0% {
      -webkit-transform: rotate(0deg);
    }
    25% {
      -webkit-transform: rotate(90deg);
    }
    50% {
      -webkit-transform: rotate(180deg);
    }
    75% {
      -webkit-transform: rotate(270deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
    }
  }

  .keyboard-con {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 0 auto;
    width: 980px;
    height: 100%;
    min-height: 320px;
    overflow: hidden;
    .show-box {
      width: 100%;
      height: 40px;
      padding-left: 30px;
      color: $light-color;
      line-height: 40px;
      font-size: 28px;
      font-family: 幼圆;

      .jump {
        display: inline-block;
        background-color: #fff;
        height: 22px;
        width: 1px;
        margin-bottom: -4px;
        animation: jump_animation 1s infinite normal;
      }

      @keyframes jump_animation {
        from {
          background-color: #fff;
        }
        to {
          background-color: #0c0c0e;
        }
      }
    }

    .main-box {
      width: 100%;
      .ul {
        display: flex;
        .li {
          display: inline-block;
          margin: 6.4px;
          text-align: center;
          font-size: 16px;
          letter-spacing: 1px;
          border-radius: 5px;
        }
      }
    }
  }
}
</style>